const monthFormat = (number) => {
    let result
    const year = Math.floor(number/12)
    const month=(number%12).toFixed(1)

    if(year!==0 && month != 0){
        result = year+' '+month+''
    }else if(year ===0 && month != 0){
        result = month+''
    }else if(year !==0 && month == 0){
        result = year+''
    }else{
        result=''
    }
    return result
}

export default monthFormat