import React, { Component } from 'react';
import Chart from 'react-apexcharts'
import styles from './Totalresult.module.css'
import {moneyFormat,monthFormat} from '../../../utils'
// import Chart from 'react-apexcharts'

class Totalresult extends Component {
    constructor(props){
        super(props)
        const {selectedIndex, mabuCount, mabuAvg, type } =this.props
        this.state={
            options: {
                chart: {
                  stacked: true,
                  stackType: '100%',
                  toolbar: {
                    show: false
                  }
                },
                plotOptions: {
                  bar: {
                    horizontal: true,
                  },
                },
                dataLabels: {
                  style: {
                    fontSize: '18px',
                    fontFamily: 'hgg, snas-serif',
                  },
                  dropShadow: {
                    enabled: true,
                  },
                },
                stroke: {
                  width: 0,
                },
                xaxis: {
                  show:false,
                  showAlways: false,
                  labels: {
                    show: false
                  },
                  axisBorder: {
                    show: false
                  },
                  axisTicks:{
                    show:false
                  }
                },
                yaxis: {
                  show:false,
                  showAlways: false,
                  labels: {
                    show: false
                  },
                  axisBorder: {
                    show: false
                  },
                  axisTicks:{
                    show:false
                  }
                },
                colors: ['#a18529', '#cab44b', '#0099a8' ,'#1dcad3'],
                fill: {
                  colors: ['#a18529', '#cab44b', '#0099a8' ,'#1dcad3'],
                  opacity: 1,
                },
        
                legend: {
                  position: 'bottom',
                  horizontalAlign: 'center',
                  onItemClick: {
                    toggleDataSeries: false
                  },
                },
                tooltip: {
                  enabled: true,
                  // custom: undefined,
                  style: {
                    fontSize: '14px',
                    fontFamily: 'hgg, sans-serif'
                  },
                  onDatasetHover: {
                      highlightDataSeries: false,
                  },
                  x: {
                      show: false,
                  },  
              }
              },
              options2: {
                chart: {
                  stacked: true,
                  stackType: '100%',
                  toolbar: {
                    show: false
                  }
                },
                plotOptions: {
                  bar: {
                    horizontal: true,
                  },
                },
                dataLabels: {
                  style: {
                    fontSize: '18px',
                    fontFamily: 'hgg, snas-serif',
                  },
                  dropShadow: {
                    enabled: true,
                  }
                },
                stroke: {
                  width: 0,
                },
                xaxis: {
                  show:false,
                  showAlways: false,
                  labels: {
                    show: false
                  },
                  axisBorder: {
                    show: false
                  },
                  axisTicks:{
                    show:false
                  }
                },
                yaxis: {
                  show:false,
                  showAlways: false,
                  labels: {
                    show: false
                  },
                  axisBorder: {
                    show: false
                  },
                  axisTicks:{
                    show:false
                  }
                },
                colors: ['#a18529', '#cab44b', '#0099a8' ,'#1dcad3'],
                fill: {
                  colors: ['#a18529', '#cab44b', '#0099a8' ,'#1dcad3'],
                  opacity: 1,
                },
        
                legend: {
                  position: 'bottom',
                  horizontalAlign: 'center',
                  onItemClick: {
                    toggleDataSeries: false
                  },
                },
                tooltip: {
                  enabled: true,
                  // custom: undefined,
                  style: {
                    fontSize: '14px',
                    fontFamily: 'hgg, sans-serif'
                  },
                  onDatasetHover: {
                      highlightDataSeries: false,
                  },
                  x: {
                      show: false,
                  },
                //   fixed: {
                //     enabled: true,
                //     position: 'topRight',
                //     offsetX: 0,
                //     offsetY: 0,
                // },
                  custom: function({series, seriesIndex, dataPointIndex, w}) {
                        if(typeof series !== "undefined") {
                            if (seriesIndex===0){
                            return '<div class="bartooltip">' +
                            '<div ><span class="tooltiptitle">' + w.globals.seriesNames[seriesIndex] +': </span>'+ series[seriesIndex][dataPointIndex] + '</div>' +
                            '<div> <span class="tooltipsub">: </span>' + monthFormat(mabuAvg[selectedIndex][seriesIndex]) + '</div>'+
                            '</div>'
                            }
                            else if (seriesIndex===1){
                              return '<div class="bartooltip">' +
                              '<div><span class="tooltiptitle">' + w.globals.seriesNames[seriesIndex] +': </span>'+ series[seriesIndex][dataPointIndex] + '</div>' +
                              '<div><span class="tooltipsub">:  </span>' + monthFormat(mabuAvg[selectedIndex][seriesIndex][0]) + '</div>'+
                              '<div><span class="tooltipsub"> </span>' + monthFormat(mabuAvg[selectedIndex][seriesIndex][1]) + '</div>'+
                              '</div>'
                              }
                            else if (seriesIndex===2){
                              return '<div class="bartooltip">' +
                              '<div ><span class="tooltiptitle">' + w.globals.seriesNames[seriesIndex] +': </span>'+ series[seriesIndex][dataPointIndex] + '</div>' +
                              '<div> <span class="tooltipsub">: </span>' + moneyFormat(mabuAvg[selectedIndex][seriesIndex]) + '</div>'+
                              '</div>'
                                }
                           else if (seriesIndex===3){
                                  return
                                }           
                        }                
                  },
  
              }
              },
              series1: [{
                name: '',
                data: []
              }, {
                name: ' ',
                data: []
              }, {
                name: '',
                data: []
              }, {
                name: ' ',
                data: []
              }],

              series2: [{
                name: '',
                data: [mabuCount[selectedIndex][0]]
              }, {
                name: ' ',
                data: [mabuCount[selectedIndex][1]]
              }, {
                name: '',
                data: [mabuCount[selectedIndex][2]]
              }, {
                name: ' ',
                data: [mabuCount[selectedIndex][3]]
              }],
        }
    }


    componentWillReceiveProps(props, state){
      const{ getdata, typefilter, filtering, getAverage} =props
      if(getdata!==null){
        
        const type0count=filtering(getdata,...typefilter[0])
        const type0avg=getAverage(type0count.map(d=>d.answer2))
    
        const type1count=filtering(getdata,...typefilter[1])
        const type1avg=getAverage(type1count.map(d=>d.answer2))
        const type1avgprobation=getAverage(type1count.map(d=>d.answer3))
    
        const type2count=filtering(getdata,...typefilter[2])
        const type2avg=getAverage(type2count.map(d=>d.answer2))
    
        const type3count=filtering(getdata,...typefilter[3])
        const type3avg=getAverage(type3count.map(d=>d.answer2))
        const type3avgprobation=getAverage(type3count.map(d=>d.answer3))

      this.setState({
        series1:[{
          name: '',
          data: [type0count.length]
        }, {
          name: ' ',
          data: [type1count.length]
        }, {
          name: '',
          data: [type2count.length]
        }, {
          name: ' ',
          data: [type3count.length]
        }],
        options:{
          tooltip: {
            custom: function({series, seriesIndex, dataPointIndex, w}) {
        
              if (seriesIndex===0){
                return '<div class="bartooltip">' +
                '<div ><span class="tooltiptitle">' + w.globals.seriesNames[seriesIndex] +': </span>'+ series[seriesIndex][dataPointIndex] + '</div>' +
                '<div> <span class="tooltipsub">: </span>' + monthFormat(type0avg.toFixed(1)) + '</div>'+
                '</div>'
              }
              else if (seriesIndex===1){
                return '<div class="bartooltip">' +
                '<div ><span class="tooltiptitle">' + w.globals.seriesNames[seriesIndex] +': </span>'+ series[seriesIndex][dataPointIndex] + '</div>' +
                '<div> <span class="tooltipsub">:  </span>' + monthFormat(type1avg.toFixed(1)) + '</div>'+
                '<div><span class="tooltipsub"> </span>' + monthFormat(type1avgprobation.toFixed(1)) + '</div>'+
                '</div>'
              }
              else if (seriesIndex===2){
                return '<div class="bartooltip">' +
                '<div ><span class="tooltiptitle">' + w.globals.seriesNames[seriesIndex] +': </span>'+ series[seriesIndex][dataPointIndex] + '</div>' +
                '<div> <span class="tooltipsub">: </span>' + moneyFormat(type2avg.toFixed(0)) + '</div>'+
                '</div>'
              }
              else if (seriesIndex===3){
                return '<div class="bartooltip">' +
                '<div ><span class="tooltiptitle">' + w.globals.seriesNames[seriesIndex] +': </span>'+ series[seriesIndex][dataPointIndex] + '</div>' +
                '<div> <span class="tooltipsub">:  </span>' + moneyFormat(type3avg.toFixed(0)) + '</div>'+
                '<div><span class="tooltipsub"> </span>' + monthFormat(type3avgprobation.toFixed(1)) + '</div>'+
                '</div>'
              }

            }
          },
        }
      })
    }
    }
    render() {
        const {
            selectedIndex,
            mabuCount,
            getdata,
            criminals,
        }=this.props

    return (
        <div className={styles.wrapper}>
            <div className={styles.siminchart}>
              <div className={styles.subtitle}>    ' ' ?</div>  
              <p className={styles.total}> <span> {getdata.length}</span> </p>
              <Chart options={this.state.options} series={this.state.series1} type="bar" height="150"/>
            </div>
            <div className={styles.mabuchart}>
              <div className={styles.subtitle}>'{criminals[selectedIndex].summary}'   ?</div>
              <p className={styles.total}>2018  5  1  <span> {mabuCount[selectedIndex][4]}</span></p>
              <Chart options={this.state.options2} series={this.state.series2} type="bar" height="150" />
            </div>
        </div>
        )
    }
}

export default Totalresult;
