import React from 'react';
import styles from './Intro.module.css'
import classnames from 'classnames/bind'

const cx = classnames.bind(styles)
const sex = [
    {key:0,
     val:''},
    {key:1,
    val:''}
]
const age = [
    {key:0,
     val:'10'},
    {key:1,
    val:'20'},
    {key:2,
    val:'30'},
    {key:3,
    val:'40'},
    {key:4,
    val:'50'},
    {key:5,
    val:'60 '}
]

const Intro = ({
    selectedSex,
    selectedAge,
    selectSex,
    selectAge
}) => {
    return (
        <div className={styles.wrapper}>
        
        <div className={styles.btncontainer}>
            <ul className={styles.selectage}>
            {age.map(a =>(
                <li 
                    key={a.key}
                    className={cx('age',{selected: selectedAge === a.key})} 
                    >
                    <button 
                        type="button" 
                        className={cx('button',{selected: selectedAge === a.key})} 
                        onClick={selectAge}
                        name={a.key}
                    >
                        {a.val}
                    </button>
                </li>
            ))}
            </ul>
            <ul className={styles.selectsex}>
            {sex.map(s =>(
                <li 
                    key={s.key}
                    className={cx('sex',{selected: selectedSex === s.key})} 
                    >
                    <button 
                        type="button" 
                        className={cx('button',{selected: selectedSex === s.key})} 
                        onClick={selectSex}
                        name={s.key}
                    >
                        {s.val}
                    </button>
                </li>
            ))}
            </ul>
            </div>
            <div className={styles.userinfo}>
                '' .<br/>
                  <span className={styles.impact}></span>  .<br/>
                  5    ?
            </div>
        </div>
    );
};

export default Intro;